/*
* Element: Label
*/
.label {
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  color: #363636;
  display: inline-block;
  max-width: 100%;
  margin-bottom: 5px;
  font-weight: bold;
}
.label--block {
  display: block;
}
/*
* Element: Label
*/
.textarea {
  display: block;
  width: 100%;
  min-height: 100px;
  padding: 10px;
  font-size: 16px;
  line-height: 1;
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  color: #363636;
  background-color: #ffffff;
  background-image: none;
  border: 1px solid #d7d7d7;
  border-radius: 10px;
  resize: none;
}
.textarea:focus {
  outline: none;
}
/*
* Element: Input fields
*/
.input {
  display: block;
  width: 100%;
  height: 40px;
  padding: 0 20px;
  font-size: 16px;
  line-height: 1;
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  color: #363636;
  background-color: #ffffff;
  background-image: none;
  border: 1px solid #d7d7d7;
  border-radius: 100px;
  vertical-align: bottom;
}
.input:focus {
  outline: 0;
}
